import React, { Component } from 'react';
import {FormField, Form} from 'react-pattern-library';

class RegisterUser extends Component {

  constructor(props) {
      super(props);

  }

  render() {
    return (
        <form id="registration-form" >
          <label>
            Email
            <input name="email" id="email-box" type="text" defaultValue=''/>
          </label>
          <label>
            Password
            <input name="password" id="password-box" type="text" defaultValue='' />
          </label>
          <button type='submit' id="register-button" onClick={this.props.onRegister}>Register</button>
        </form>
    )
  }
}

export default RegisterUser



